import React from 'react';
import Link from 'next/link';
import { FaHome } from 'react-icons/fa';
import Layout from '@/components/layout'; // Import Layout component

export default function LoginPage() {
  return (
    <Layout> {/* Wrap the content with the Layout component */}
      <div
        className="min-h-screen flex items-center justify-center bg-gray-900 text-white bg-cover bg-center"
        style={{ backgroundImage: 'url(/login-bg.jpg)' }}
      >
        {/* aCloudBridge Button */}
        <Link
          href="/"
          className="absolute top-2 left-7 bg-[#e16f57] text-[#144eb9] py-2 px-4 rounded-full flex items-center"
        >
          <FaHome className="text-[#144eb9] mr-3" />
          aCloudBridge
        </Link>

        <div className="bg-[#063970] p-8 rounded-lg shadow-md w-112">
          <h2 className="text-2xl font-bold mb-6 text-center">Sign in to aCloudBridge</h2>
          <form>
            <div className="mb-4">
              <input
                type="email"
                className="w-full px-3 py-2 border rounded-md bg-[#251228] focus:outline-none focus:ring focus:ring-blue-500"
                placeholder="Email"
              />
            </div>
            <div className="mb-6">
              <input
                type="password"
                className="w-full px-3 py-2 border rounded-md bg-[#251228] focus:outline-none focus:ring focus:ring-blue-500"
                placeholder="Password"
              />
            </div>
            <div className="flex items-center justify-between mb-4">
              <a href="#" className="text-blue-500 text-sm">Forgot your Password?</a>
              <div className="flex items-center">
                <input type="checkbox" id="remember" className="mr-2" />
                <label htmlFor="remember" className="text-sm">Remember me</label>
              </div>
            </div>
            <button
              type="submit"
              className="w-full bg-[#67c1e2] text-white py-2 px-4 rounded-md"
            >
              SIGN IN
            </button>
          </form>
          <div className="mt-4 text-center">
            <p className="text-sm">
              Not signed up yet? <a href="/signup" className="text-blue-500">SIGN UP NOW</a>
            </p>
          </div>
        </div>
      </div>
    </Layout>
  );
}